.tags-detail-page {
  min-height: 100vh;
  background-color: #000000;
  padding-top: 80px;
}

.tags-detail-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  color: #ffffff;
}

.detail-title {
  font-size: clamp(2rem, 5vw, 3rem);
  margin-bottom: 2rem;
  background: linear-gradient(45deg, #00f2fe, #4facfe);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-shadow: 0 0 20px rgba(79, 172, 254, 0.5);
  text-align: center;
}

.tag-description {
  font-size: 1.2rem;
  line-height: 1.6;
  text-align: center;
  max-width: 800px;
  margin: 0 auto 3rem;
}

.specifications-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  margin: 3rem 0;
}

.spec-card {
  background: rgba(0, 242, 254, 0.05);
  padding: 1.5rem;
  border-radius: 15px;
  border: 1px solid rgba(0, 242, 254, 0.1);
  transition: all 0.3s ease;
}

.spec-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 5px 20px rgba(0, 242, 254, 0.2);
  border-color: rgba(0, 242, 254, 0.3);
}

.spec-card h3 {
  color: #00f2fe;
  margin-bottom: 1rem;
  font-size: 1.3rem;
}

.features-section, .applications-section {
  margin: 3rem 0;
}

.features-section h2, .applications-section h2 {
  color: #00f2fe;
  font-size: 1.8rem;
  margin-bottom: 1.5rem;
  text-align: center;
}

.features-list, .applications-list {
  list-style: none;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1rem;
}

.features-list li, .applications-list li {
  padding: 1rem 1.5rem;
  background: rgba(0, 242, 254, 0.05);
  border-radius: 8px;
  margin: 0.5rem 0;
  position: relative;
  padding-left: 2.5rem;
}

.features-list li:before, .applications-list li:before {
  content: '→';
  position: absolute;
  left: 1rem;
  color: #00f2fe;
}

@media screen and (max-width: 768px) {
  .tags-detail-content {
    padding: 1rem;
  }

  .specifications-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }

  .features-list, .applications-list {
    grid-template-columns: 1fr;
  }

  .tag-description {
    font-size: 1.1rem;
    padding: 0 1rem;
  }
}

.specs-table {
  margin: 3rem 0;
  overflow-x: auto;
}

.specs-table table {
  width: 100%;
  border-collapse: collapse;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 15px;
}

.specs-table th,
.specs-table td {
  padding: 1rem;
  text-align: left;
  border-bottom: 1px solid rgba(0, 242, 254, 0.1);
}

.specs-table th {
  background: rgba(0, 242, 254, 0.1);
  color: #00f2fe;
  font-weight: 600;
}

.specs-table tr:hover {
  background: rgba(255, 255, 255, 0.08);
}

.detailed-info {
  margin: 3rem 0;
}

.detailed-info h2 {
  color: #00f2fe;
  font-size: 1.8rem;
  margin: 2rem 0 1rem;
}

.detailed-info p {
  color: #ffffff;
  line-height: 1.8;
  margin-bottom: 2rem;
  opacity: 0.9;
}

@media screen and (max-width: 768px) {
  .specs-table {
    margin: 2rem -1rem;
    padding: 0 1rem;
  }
  
  .specs-table th,
  .specs-table td {
    padding: 0.8rem;
    font-size: 0.9rem;
  }
  
  .detailed-info h2 {
    font-size: 1.5rem;
  }
} 